import React from "react";
import { makeStyles } from "@material-ui/styles";
import gridBackground from "../../../assets/images/squares.png";
import { Box, Grid, Typography } from "@material-ui/core";
import Historia from "../../ui/Historia";
import MarcoLegal from "../../ui/MarcoLegal";

const useStyles = makeStyles((theme) => ({
  backGround: {
    background: `url(${gridBackground})`,
  },
}));

export default function HistoriaPage() {
  const classes = useStyles();
  return (
    <div
      className={classes.backGround}
      style={{
        minHeight: "100vh",
        position: "relative",
        /* paddingBottom: "20em", */
      }}
    >
      <Grid container alignItems="flex-start">
        <Grid item xs={8}>
          <Box p={2} m={2}>
            <Grid container direction="column" style={{ padding: "16px" }}>
              <Grid item>
                <Typography variant="h3">
                  CAPRES: Organizacin Administrativa
                </Typography>
              </Grid>
              <Grid item style={{ padding: "16px" }}>
                <p>
                  <Typography>Asesora Jurdica</Typography>
                  Es la instancia de consulta legal en lo relativo a la
                  interpretacin de toda la normativa legal que rige la Caja de
                  Ahorro, as como de las decisiones, acuerdos y resoluciones
                  que adopten los Consejos de Administracin y de Vigilancia, y
                  la Asamblea General de Delegados.
                </p>

                <p>
                  <Typography>Gerente Ejecutivo</Typography>
                  Es designado por el Consejo de Administracin, siendo sus
                  deberes y atribuciones:
                </p>
                <ul>
                  <li>
                    Coordinar y supervisar directamente al personal que ocupa
                    las Jefaturas de las Divisiones de CAPRES e indirectamente
                    al resto del personal de cada Divisin.
                  </li>
                  <li>
                    Los dems deberes y atribuciones que le establezca el
                    Consejo de Administracin.
                  </li>
                </ul>
              </Grid>
              <Grid item style={{ padding: "16px" }}>
                <Typography>
                  La Organizacin Administrativa de CAPRES est conformada por
                  cinco (5) Divisiones:
                </Typography>
                <ul>
                  <li>
                    <p>
                      <span>
                        <strong>Divisin de Operaciones: </strong>
                      </span>
                      Su labor principal es la administracin de la informacin
                      de los afiliados y la prestacin de los servicios que
                      estos requieran, con relacin a prstamos, retiros de
                      haberes y reintegros, entre otros.
                    </p>
                  </li>
                  <li>
                    <p>
                      <span>
                        <strong>Divisin de Finanzas: </strong>
                      </span>
                      Area responsable de la liquidacin de los procesos
                      generados por la Divisin de Operaciones y la de Planes y
                      Beneficios socio-econmicos adems de la elaboracin,
                      manejo y ejecucin del presupuesto, administrar, controlar
                      y custodiar las inversiones que realice CAPRES y dems
                      valores financieros.
                    </p>
                  </li>
                  <li>
                    <p>
                      <span>
                        <strong>Divisin de Contabilidad: </strong>
                      </span>
                      Es el rea responsable del manejo de la Contabilidad y la
                      elaboracin y presentacin del Balance General y el Estado
                      de Prdidas y Ganancias.
                    </p>
                  </li>
                  <li>
                    <p>
                      <span>
                        <strong>
                          Divisin de Planes de Inversiones y Beneficios
                          Socio-Econmicos:{" "}
                        </strong>
                      </span>
                      Es la encargada de la ejecucin de los programas de
                      beneficios sociales que realiza CAPRES, tales como
                      otorgamiento de Crditos con Garanta hipotecaria y
                      crditos para la adquisicin de vehculos entre otros.
                    </p>
                  </li>
                  <li>
                    <p>
                      <span>
                        <strong>Divisin de Informtica: </strong>
                      </span>
                      Responde por el asesoramiento en cuanto a Sistemas de
                      informacin adems de mantener los equipos de computacin
                      operativos, dar soporte al personal de CAPRES en las
                      operaciones que stos realizan.
                    </p>
                  </li>
                </ul>
              </Grid>
            </Grid>
          </Box>
        </Grid>
        <Grid item xs={4}>
          <Box p={2} m={2}>
            <Grid
              container
              direction="column"
              justify="space-around"
              alignItems="stretch"
            >
              <Grid item>
                <Historia />
              </Grid>

              <Grid item>
                <MarcoLegal />
              </Grid>
            </Grid>
          </Box>
        </Grid>
      </Grid>
    </div>
  );
}
