import { Box, Grid, makeStyles, Typography } from "@material-ui/core";
import React from "react";
import gridBackground from "../../../assets/images/squares.png";

const useStyles = makeStyles((theme) => ({
  backGround: {
    background: `url(${gridBackground})`,
  },
}));

export default function ViviendaPages() {
  const classes = useStyles();
  return (
    <div
      className={classes.backGround}
      style={{
        minHeight: "100vh",
        position: "relative",
        /* paddingBottom: "20em", */
      }}
    >
      <Box>
        <Grid container direction="column" style={{ padding: "2rem" }}>
          <Grid item container direction="column">
            <Typography variant="h4" style={{ fontSize: "3rem" }}>
              Prstamos para Vivienda
            </Typography>
            <Typography variant="body1">
              CAPRES otorgar prstamos a los Asociados que tengan ms de:
            </Typography>
            <ul>
              <li>
                Seis (6) meses de antigedad en el caso de Prstamos
                Hipotecarios.
              </li>
              <li>
                Seis (6) meses de antiguedad en el caso de Prstamos para
                Remodelacin de Vivienda Principal.
              </li>
            </ul>
          </Grid>
          <Box item p={2}>
            <Grid item container direction="column">
              <Typography variant="h3">
                Prstamos para adquisicin de Vivienda Principal
              </Typography>
              <p>
                Los prestamos especiales para adquisicin de vivienda principal
                tienen un plazo para la cancelacion de Doce (12) aos, a una
                tasa del 10% anual, descontando cuotas anuales especiales
                provenientes de hasta tres (3) meses de aguinaldo y mes y medio
                (1,5) de la doble remuneracion.
              </p>
              <Typography variant="body1">
                Requisitos para solicitar prstamos para adquisicin de
                vivienda:
              </Typography>
              <ul>
                <li>Solicitud debidamente sellada y firmada.</li>
                <li>Fotocopia de la cedula de identidad.</li>
                <li>
                  Fotocopia de recibo de pago de la 2da. Quincena  Reciente.
                </li>
                <li>
                  Inscribirse en el libro de solicitudes que lleva el Delegado
                  de la Regin a la cual pertenece el Asociado.
                </li>
              </ul>
            </Grid>
          </Box>
          <Box item p={2}>
            <Grid item container direction="column">
              <Typography variant="h3">
                Prstamos para Remodelacion de Vivienda Principal:
              </Typography>
              <p>
                Los prestamos para Remodelacin de vivienda principal tienen un
                plazo para la cancelacion de Seis (6) aos, a una tasa del 12%
                anual, descontando cuotas anuales especiales provenientesd de
                hasta dos (2) meses de aguinaldo y Un mes (1) de la doble
                remuneracion.
              </p>
              <ul>
                <li>
                  De los aguinaldos en un mximo de cuatro (4) cuotas, dos (2)
                  por cada ao.
                </li>
                <li>
                  De la Doble Remuneracin, con un mximo de cuatro (4) cuotas,
                  una (1) por cada ao.
                </li>
              </ul>
              <Typography variant="body1">
                Requisitos para solicitar prstamos para remodelacin de
                vivienda:
              </Typography>
              <ul>
                <li>Solicitud debidamente sellada y firmada.</li>
                <li>Fotocopia de la cdula de identidad.</li>
                <li>
                  Fotocopia de recibo de pago de la 2da. quincena. Reciente.
                </li>
                <li>
                  Inscribirse en el libro de solicitudes que lleva el delegado
                  de la regin a la cual pertenece el asociado
                </li>
              </ul>
            </Grid>
          </Box>
        </Grid>
      </Box>
    </div>
  );
}
